"use client"
import React, { useState } from 'react'
import './login.scss'
import { wixClientServer } from '@/lib/wixClientServer';


const MODE =  {
    LOGIN : "LOGIN",
    REGISTER : "REGISTER",
    RESET_PASSWORD : "RESET_PASSWORD",
    EMAIL_VERIFICATION : "EMAIL_VERIFICATION",
  }

const LoginPage = () => {
    const [mode , setMode] = useState(MODE.LOGIN)
    const [isLoading , setIsLoading ] = useState(false);
    const [error , setError] = useState("")
    const [message , setMessage ] = useState("")

    const formTitle = mode===MODE.LOGIN ? "Log in" : mode===MODE.REGISTER ? "Register" : mode===MODE.RESET_PASSWORD ? "Reset your password" : "verify your Email";
    const buttonTitle = mode===MODE.LOGIN ? "Login" : mode===MODE.REGISTER ? "Register" : mode===MODE.RESET_PASSWORD ? "Reset" : "verify";

    const wixClient = wixClientServer();

  return (
    <section className='login_page'>
        <div className='container'>
            <div className='login_box'>
                <h2 className='title'>{formTitle}</h2>
                <div className='form_'>
                    {mode===MODE.REGISTER ? (
                        <div className='form_control'>
                            <label>Username</label>
                            <input type='text' placeholder='John' name='username' />
                        </div>
                    ):(
                        null
                    )}
                    {mode !==MODE.EMAIL_VERIFICATION ? (
                        <div className='form_control'>
                            <label>E mail</label>
                            <input type='email' placeholder='john@gmail.com' name='email' />
                        </div>) : (
                        <div className='form_control'>
                            <label>Verification Code</label>
                            <input type='text' placeholder='Code ' name='enailcode' />
                        </div>
                    )}
                    {mode === MODE.LOGIN || mode === MODE.REGISTER ? (
                        <div className='form_control'>
                            <label>Password</label>
                            <input type='password' placeholder='Enter password' name='passowrd' />
                        </div>
                    ):(
                        null
                    )}
                   <button className='cmnBtn' disabled>{isLoading ? "Loading..." : buttonTitle}</button>
                    
                    {error && <div className='error'>{error}</div>}

                    {mode === MODE.LOGIN && (
                        <span className='frgt_txt' onClick={()=>setMode(MODE.REGISTER)}>
                            {"Don't"} have an account?
                        </span>
                    )}
                   {mode === MODE.REGISTER && (
                        <div
                            className="text-sm underline cursor-pointer"
                            onClick={() => setMode(MODE.LOGIN)}
                        >
                            Have and account?
                        </div>
                        )}
                        {mode === MODE.RESET_PASSWORD && (
                        <div
                            className="text-sm underline cursor-pointer"
                            onClick={() => setMode(MODE.LOGIN)}
                        >
                            Go back to Login
                        </div>
                        )}
                        {message && <div className="text-green-600 text-sm">{message}</div>}

                </div>
            </div>
        </div>
    </section>
  )
}


export default LoginPage;
